import {GET_INITIAL_USERS,SET_MODE,GET_USERS, GET_SEARCH_USER,GET_LOGINUSER, GET_ALLUSER,ClEAR_USER, SET_LOADING } from '../type'

export default (state, action) => {
    const { type, payload = {} } = action
    const { initialUser, mode,users,usersNum,searchUserNum,searchUser,loading, list,allUser,total } = payload

    switch (type) {
        case SET_LOADING:
            return {
                ...state, //对state中状态字段解构
                loading: loading
            };
        case SET_MODE:
            return {
                ...state, //对state中状态字段解构
                mode: mode
            };   
        case ClEAR_USER:
            return {
                ...state,
                loginUser: {}
            };
        case GET_LOGINUSER:
            return {
                ...state,
                list: list,
            };
        case GET_ALLUSER:
            return {
                ...state,
                allUser: allUser,
                total:total,
            };
        case GET_SEARCH_USER:
            return{
                ...state,
                searchUser: searchUser,
                searchUserNum: searchUserNum,
            }    
        case GET_USERS:
            return{
                ...state,
                users: users,
                usersNum: usersNum,
            }  
        case GET_INITIAL_USERS:
            return{
                ...state,
                initialUser:initialUser,
                
            }      

        default:
            return state;
    }

}