import {GET_INFO,CLEAR_MESS,GET_MESS,GET_PIC,SET_LOADING } from '../type'

export default (state, action) => {
    const { type, payload = {} } = action
    const { title,content,info, mess,messCount,pic,loading } = payload

    switch (type) {
        case SET_LOADING:
            return {
                ...state, //对state中状态字段解构
                loading: loading
            };
    
        case GET_PIC:
            return {
                ...state,
                pic: pic,
            };
        
        case GET_MESS:
            return {
                ...state,
                mess: mess,
                messCount: messCount
            }    

        case CLEAR_MESS:
            return {
                ...state,
                mess: [],
                messCount: 0,
            }   
            
        case GET_INFO:
            return {
                ...state,
                info: info,
                title: title,
                content: content
            }    
         

        default:
            return state;
    }

}