import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { Link, NavLink } from 'react-router-dom'  //用于链接跳转路由，替换a标签，不会造成页面刷新，并且可以缓存数据
import { Menu, Dropdown, Icon, message, Popconfirm } from 'antd'
import axios from 'axios'

const Navbar = (props) => {
    const { title } = props
    let name = localStorage.getItem('name')
    let login = localStorage.getItem('login')
    let mess = login ? name + '，欢迎' : '请登陆'

    useEffect(() => {  //useEffect 重构生命周期didMount -- 记住用户的登陆
        let name = localStorage.getItem('name')
        let login = localStorage.getItem('login')
        console.log(name, login)
        if (!login) {
            message.info('请您先登陆！');
            setTimeout(() => {
                window.location.href = '/'
            }, 1000)
        }
        else{
            checkToken() //若已登陆，检查当前sessionId是否过期
        }
        // eslint-disable-next-line
    }, [])

     //前台sessionId检测
     const checkToken =  () => {
        axios.post('/zjj/user/check', {},
        {
            headers: {
                'username': name,
            }
        }
        ).then((data) => {
            if (data.status !== 200) {
                message.info('网络错误！')
                return
            }
            if (data.data.err === -999) {
             return Promise.reject('登陆过期，请您重新登陆')
            }
        }, () => {
            return Promise.reject('登陆过期，请您重新登陆')
        })
            .catch((err) => {
                console.log(err);
                message.info(err);
                localStorage.removeItem('name') //删除本地缓存
                localStorage.removeItem('login')
                localStorage.removeItem('mode')
                localStorage.removeItem('_id')
                localStorage.removeItem('right') //1--管理员权限；0--普通用户

                setTimeout(() => {
                    window.location.href = '/'
                }, 1000)
            });
    }

    const handleToNotice = (e) =>{
        let rt = String(localStorage.getItem('right'))
        console.log('rt',typeof rt)
        if(rt !== '1' && rt !== '2'){
            e.preventDefault()
            message.info('当前用户无任务发布权限！')
        }
    }

    const handleToMonitor = (e) =>{
        let rt = String(localStorage.getItem('right'))
        console.log('rt',typeof rt)
        if(rt !== '1' && rt !== '3'){
            e.preventDefault()
            message.info('当前用户无任务监控权限！')
        }
    }

    const logOut = () => { //摧毁sessionId
        axios.post('/zjj/user/logOut', {}).then((data) => {
            console.log(data, login)
            if (data.status === 200) {
                if (data.data.err === 0) {
                    //注销成功
                    message.info('用户注销成功！');
                    localStorage.removeItem('name') //删除本地缓存
                    localStorage.removeItem('login')
                    localStorage.removeItem('mode')
                    localStorage.removeItem('_id')
                    localStorage.removeItem('right') //1--管理员权限；0--普通用户
                    setTimeout(() => {
                        // props.history.push({ pathname: '/' })
                        window.location.href = '/'
                    }, 1000)

                } else {
                    message.info('用户注销失败，请重试！');
                    return false
                }
            }
        })
    }

    const cancel = () => {
        message.info('已取消注销')
    }

    const menu = (
        <Menu>
            <Menu.Item>

                {login &&
                    <Popconfirm
                        title="退出登陆"
                        // width={100}
                        style={{ width: '1000px' }}
                        onConfirm={logOut}
                        onCancel={cancel}
                        okText="Yes"
                        cancelText="No"
                    >
                        <span href="#">退出登陆</span>
                    </Popconfirm>
                }

                {!login && <Link to="/">
                    请您登陆
                </Link>}
            </Menu.Item>
        </Menu>
    );

    return (
        <nav className="navbar App-header">
            <div className='nav-color'>
                {title}
            </div>
            <ul className="nav_ul" style={{right:'24%'}}>
                <li><NavLink className="link" activeClassName="nav_active" to="/index">功能中心</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/info">公示公告</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" onClick={handleToNotice} to="/notice">任务发布</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" onClick={handleToMonitor} to="/monitor">任务监控</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/file">文件管理</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/people">用户中心</NavLink></li>
            </ul>
            <ul className='nav_ul2'>
                <Dropdown overlay={menu} className='dropDown'>
                    {/* // eslint-disable-next-line  */}
                    <li className="link"
                        style={{ fontSize: '21px',     
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                        maxWidth: '220px'}}
                    >
                        {mess}
                        <Icon className='navIcon'
                            spin
                            style={{
                                fontWeight: 'bolder', fontSize: '25px'
                                , position: 'relative', top: '2px'
                            }}
                            type="down" />
                    </li>
                </Dropdown>
            </ul>
        </nav>
    )
}

Navbar.defaultProps = {  //设置默认值
    title: '安全管理监控分析系统',
};

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
};
export default Navbar
