import React ,{useEffect}from 'react'
import PropTypes from 'prop-types'
import { Link,NavLink } from 'react-router-dom'  //用于链接跳转路由，替换a标签，不会造成页面刷新，并且可以缓存数据
import { Menu, Dropdown, Icon, message,Popconfirm } from 'antd'
import axios from 'axios'

const Navbar = (props) => {
    const {  title } = props
    let name = localStorage.getItem('name')
    let login = localStorage.getItem('login')
    let mess = login ? name+'，欢迎' : '请登陆'
    let _id =localStorage.getItem('_id') 

    useEffect(()=>{
        if(!login){
            message.info('请您先登陆！')
            setTimeout(()=>{
                window.location.href = '/'
            },1000)
        }
        else{
            checkToken(_id)  //登陆检测、管理员身份认证
        }
    },[])

    //后台token检测，同时鉴别是否管理员身份
    const checkToken =  (_id) => {
        axios.post('/zjj/user/checkBack', {_id},
        {
            headers: {
                'username': name,
            }
        }
        ).then((data) => {
            if (data.status !== 200) {
                message.info('网络错误！')
                return
            }
            if (data.data.err === -999) {
             return Promise.reject('登陆过期，请您重新登陆!')
            }
            if (data.data.err === -2) {
                return Promise.reject('校验失败，请重新登陆!')
            }
            if (data.data.err === -1) {
                return Promise.reject('当前用户不是管理员！')
            }

        })
            .catch((err) => {
                console.log(err);
                message.info(err);
                localStorage.removeItem('name') //删除本地缓存
                localStorage.removeItem('login')
                localStorage.removeItem('mode')
                localStorage.removeItem('_id')
                localStorage.removeItem('right') //1--管理员权限；0--普通用户

                setTimeout(() => {
                    window.location.href = '/'
                }, 1000)
            });
    }

    const logOut = () => { //摧毁sessionId
        axios.post('/zjj/user/logOut', {}).then((data) => {
            console.log(data, login)
            if (data.status === 200) {
                if (data.data.err === 0) {
                    //注销成功
                    message.info('用户注销成功！');
                    localStorage.removeItem('name') //删除本地缓存
                    localStorage.removeItem('login')
                    localStorage.removeItem('mode')
                    localStorage.removeItem('_id')
                    localStorage.removeItem('right') //1--管理员权限；0--普通用户

                    setTimeout(() => {
                        // props.history.push({ pathname: '/' })
                        window.location.href = '/'
                    }, 1000)

                } else {
                    message.info('用户注销失败，请重试！');
                    return false
                }
            }
        })
    }

    const cancel = () => {
        message.info('已取消注销')
    }

    const menu = (
        <Menu>
            <Menu.Item>

                {login &&
                    <Popconfirm
                        title="退出登陆"
                        // width={100}
                        style={{width:'1000px'}}
                        onConfirm={logOut}
                        onCancel={cancel}
                        okText="Yes"
                        cancelText="No"
                    >
                        <span href="#">退出登陆</span>
                    </Popconfirm>
                }

                {!login && <Link to="/">
                    请您登陆
                </Link>}
            </Menu.Item>
        </Menu>
    );

    return (
        <nav className="navbar App-header">
            <div className='nav-color'>
                {title}
            </div>
            <ul className="nav_ul">
                <li><NavLink className="link" activeClassName="nav_active" to="/indexBack">组织管理</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/authority">权限管理</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/txt">公告管理</NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/fileback">文件门户</NavLink></li>
            </ul>
            <ul className='nav_ul2'>
            <Dropdown overlay={menu} className='dropDown'>
                    {/* // eslint-disable-next-line  */}
                    <li className="link"
                      style={{ fontSize: '21px',     
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      whiteSpace: 'nowrap',
                      maxWidth: '220px'}}
                    >
                        {mess}
                        <Icon className='navIcon'
                            spin
                            style={{ fontWeight:'bolder',fontSize: '25px'
                                     ,position:'relative',top:'2px'
                                  }}
                            type="down" />
                    </li>
                </Dropdown> 
            </ul>
        </nav>
    )
}

Navbar.defaultProps = {  //设置默认值
    title: '安全管理监控分析系统后台',
};

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
};
export default Navbar
