import React, { useReducer } from 'react';
import MonitorContext from './monitorContext';
import MonitorReducer from './monitorReducer';
import axios from 'axios'
import { message } from 'antd'
import moment from 'moment'
import { CLEAR_RAW, GET_DONE_LIST, GET_MONITOR, SET_LOADING, GET_DETAIL, CLEAR_ID } from '../type'



const MonitorState = props => {
    //初始化状态
    const initialState = {
        monitor: [],
        detail: {},
        loading: false,
        total: 0,
        currentId: 0,

        doneList: [], //已触发列表
        doneListNum: 0,

        //charts图表1数据；
        iowait: [],
        idle: [],
        system: [],
        user: [],
        //chart2数据
        load1: [],
        load5: [],
        load15: [],
        //chart3数据
        total1: [],
        used: [],
        free: [],
        avaliable: [],
        shared: [],
        cached: [],
        //chart4数据
        t_in: [],
        t_out: [],
        //chart5数据
        uptime: [],

        xArr1:[],
        xArr2:[],
        xArr3:[],
        xArr4:[],
        xArr5:[],
        five: [],


    };

    //使用useReducer，即state关联Reducer
    const [state, dispatch] = useReducer(MonitorReducer, initialState) //函数参数1：当前提交的reduver       函数参数2：当前提交的状态值 ；

    //实现方法区域                                                          
    //getMonitor
    const getMonitor = async () => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        let res = null
        try {
            res = await axios.get('/api/hosts/status', {})
        } catch (err) {
            message.info('网络错误，请重试！')
            // return Promise.reject()
        }

        if (res.status !== 200) {
            message.info('请求错误')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_MONITOR,
            payload: {
                monitor: res.data,
                total: res.data.length
            }
        })
        setLoading(false)
    };
    // api/graphs/?host_id=3&time_range=latest  


    //getMonitorDetail
    const getMonitorDetail = async (id) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        let res = [],
            //chart1
            _iowait = [],
            _idle = [],
            _system = [],
            _user = [],
            //chart2
            _load1 = [], _load5 = [], _load15 = [],
            //chart3
            _total1 = [], _used = [], _free = [], _avaliable = [], _shared = [], _cached = [],
            //chart4
            _t_in = [], _t_out = [],
            //chart5
            _uptime = [],
            //other
            _xArr1 = [],
            _xArr2 = [],
            _xArr3 = [],
            _xArr4 = [],
            _xArr5 = [],
            _five = [];


        try {
            res = await axios.get(`/api/graphs/?host_id=${id}&time_range=latest`)
        }
        catch (err) {
            message.info(err)
            return Promise.reject(err)
        }
        if (res.status !== 200) {
            setLoading(false)
            return Promise.reject('请求失败，请重试！')
        }
        res.data['0'].data['1'] && res.data['0'].data['1'].raw_data.forEach(e => {
            _iowait.push(Number(eval(e)[0].iowait));
            _idle.push(Number(eval(e)[0].idle));
            _system.push(Number(eval(e)[0].system));
            _user.push(Number(eval(e)[0].user));
            _xArr1.push(moment(eval(e)[1]).format('YYYY-MM-DD HH:mm:ss').split(' ')[1])
        })
        res.data['0'].data['2'] && res.data['0'].data['2'].raw_data.forEach(e => {
            _load1.push(Number(eval(e)[0].load1));
            _load5.push(Number(eval(e)[0].load5));
            _load15.push(Number(eval(e)[0].load15));
            _xArr2.push(moment(eval(e)[1]).format('YYYY-MM-DD HH:mm:ss').split(' ')[1])

        })
        // _total1=[],_used=[],_free=[],_swap=[],_buffer=[],_cached=[],
        res.data['0'].data['3'] && res.data['0'].data['3'].raw_data.forEach(e => {
            _total1.push(Number(eval(e)[0].total));
            _used.push(Number(eval(e)[0].used));
            _free.push(Number(eval(e)[0].free));
            _avaliable.push(Number(eval(e)[0].avaliable));
            _shared.push(Number(eval(e)[0].shared));
            _cached.push(Number(eval(e)[0].cache))
            _xArr3.push(moment(eval(e)[1]).format('YYYY-MM-DD HH:mm:ss').split(' ')[1])


        })
        res.data['0'].data['4'] && res.data['0'].data['4'].raw_data.forEach(e => {
            _t_in.push(Number(eval(e)[0].data['eth0'].t_in));
            _t_out.push(Number(eval(e)[0].data['eth0'].t_out));
            _xArr4.push(moment(eval(e)[1]).format('YYYY-MM-DD HH:mm:ss').split(' ')[1])

        })
        res.data['0'].data['6'] && res.data['0'].data['6'].raw_data.forEach(e => {
            _uptime.push((eval(e)[0].uptime));
            _xArr5.push(moment(eval(e)[1]).format('YYYY-MM-DD HH:mm:ss').split(' ')[1])
            _five.push(1)
        })

        res.data['0'].data['1'] &&
            dispatch({
                type: GET_DETAIL,
                payload: {
                    detail: res.data[0].data,
                    currentId: id,
                    //chart1_data
                    iowait: _iowait,
                    idle: _idle,
                    system: _system,
                    user: _user,
                    //chart2_data
                    load1: _load1,
                    load5: _load5,
                    load15: _load15,
                    //chart3_data
                    total1: _total1,
                    used: _used,
                    free: _free,
                    avaliable: _avaliable,
                    shared: _shared,
                    cached: _cached,
                    //chart4_data
                    t_in: _t_in,
                    t_out: _t_out,
                    //chart5_data
                    uptime: _uptime,
                    //other
                    xArr1: _xArr1,
                    xArr2: _xArr2,
                    xArr3: _xArr3,
                    xArr4: _xArr4,
                    xArr5: _xArr5,
                    five: _five,

                }
            })
        setLoading(false)
    };

    //获取已触发的报警事件列表
    const getDoneList = async (id) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        let res = null
        try {
            res = await axios.get(`/api/triggers_list/?host_id=${id}`)
        }
        catch (err) {
            message.info(err)
            return Promise.reject()
        }
        if (res.status !== 200) {
            message.info('请求错误')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_DONE_LIST,
            payload: {
                doneList: res.data[0].event_list,
                doneListNum: res.data[0].event_list.length
            }
        })
        setLoading(false)
    };

    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //使用dispatch提交到Reducer中
            payload: { loading }
        })
    }

    //页面销毁，清除当前主机id
    const clearId = () => {
        dispatch({
            type: CLEAR_ID,
        })
    }

    //页面卸载，清除context存储数据
    const clearRaw = () => {
        dispatch({
            type: CLEAR_RAW
        })
    }

    return (<MonitorContext.Provider value={{
        monitor: state.monitor,
        loading: state.loading,
        total: state.total,
        detail: state.detail,
        currentId: state.currentId,
        doneList: state.doneList, //已触发主机列表
        doneListNum: state.doneListNum,

        //chart1数据
        iowait: state.iowait,
        idle: state.idle,
        system: state.system,
        user: state.user,

        //chart2数据
        load1: state.load1,
        load5: state.load5,
        load15: state.load15,

        //chart3数据
        total1: state.total1,
        used: state.used,
        free: state.free,
        avaliable: state.avaliable,
        shared: state.shared,
        cached: state.cached,

        //chart4数据
        t_in: state.t_in,
        t_out: state.t_out,

        //chart5数据
        uptime: state.uptime,

        //other
        xArr1: state.xArr1,
        xArr2: state.xArr2,
        xArr3: state.xArr3,
        xArr4: state.xArr4,
        xArr5: state.xArr5,
        five : state.five,



        setLoading,
        getMonitor,
        getMonitorDetail,
        clearId,
        getDoneList,
        clearRaw,
    }}>{props.children}</MonitorContext.Provider>)


}
export default MonitorState
