import React, { useReducer } from 'react';
import FileContext from './fileContext';
import FileReducer from './fileReducer';
import axios from 'axios'
import qs from 'qs'
import { message } from 'antd'

import { GET_SEARCH_FILE,GET_FILE,SET_MODE,SET_LOADING } from '../type'



const FileState = props => {
    //初始化状态
    const initialState = {
        loading: false,
        mode: false, //false -- 普通模式； true -- 搜索模式
        file: [], //普通模式搜索文件
        count: 0, //普通模式文件总数
        _file: [], //搜索模式搜索文件
        _count: 0, //搜索模式文件总数
        val: '', //记录当前搜索文件名
        title: '', //路由匹配的页面标题名
    
    };

    //使用useReducer，即state关联Reducer
    const [state, dispatch] = useReducer(FileReducer, initialState) //函数参数1：当前提交的reduver       函数参数2：当前提交的状态值 ；

    //实现方法区域                                                          
    
   
    //getFile ,按分类来分页获取文件具体信息
    const getFile = async (type,limit,page) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        const res = await axios.post('/zjj/myFile/getFileByType',{type,limit,page})
        if(res.data.err===-1){
            message.info('参数错误')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_FILE,
            payload: {
                file: res.data.info.list,
                count: res.data.info.count,
            }
        })
        setLoading(false)
        setMode(false)

    };
  

     //getSearchTable ,按搜索分页获取组织表格
     const _getFile = async (Kw,limit,page) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        setMode(true)//改变当前分页查询为：按搜索数据

        const res = await axios.post('/zjj/myFile/getFileByKw',{Kw,limit,page})
        if(res.data.err===-1){
            message.info('参数错误')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_SEARCH_FILE,
            payload: {
                _file: res.data.info.list,
                _count: res.data.info.count,
                val: Kw
            }
        })
        setLoading(false)
    };

    


    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //使用dispatch提交到Reducer中
            payload: { loading }
        })
    }

    //set mode : false === 未搜索数据 ;   true === 按搜索数据
    const setMode = (mode) => {
        dispatch({
            type: SET_MODE, 
            payload: { mode }
        })
    }

    return (<FileContext.Provider value={{
        //属性区域
        loading: state.loading,
        mode: state.mode,
        file: state.file,
        _file: state._file,
        count: state.count,
        _count: state._count,
        val: state.val,
        title: state.title,
        
        //方法区域
        setMode,
        setLoading,
        getFile,
        _getFile,

    }}>{props.children}</FileContext.Provider>)


}
export default FileState
