import React from 'react'
import {message, Typography } from 'antd'
import {Link} from 'react-router-dom'

import '../HotProcess/HotProcess.scss'



const HotProcess = (e) => {

    const handleToNotice = (e) =>{
        let rt = String(localStorage.getItem('right'))
        console.log('rt',typeof rt)
        if(rt !== '1' && rt !== '2'){
            e.preventDefault()
            message.info('当前用户无任务发布权限！')
        }
    }

    const handleToMonitor = (e) =>{
        let rt = String(localStorage.getItem('right'))
        console.log('rt',typeof rt)
        if(rt !== '1' && rt !== '3'){
            e.preventDefault()
            message.info('当前用户无任务监控权限！')
        }
    }
    const { Text } = Typography


    return (
        <div className='HotProcess'>
            <div className='navText'>
                <Text strong className='navFont'>推荐功能</Text>
            </div>
            <div className='navWrap'>
                <div className='navItem'>
                    <Link
                        onClick={handleToMonitor}
                        to='/monitor' 
                        className='navBox'>
                        <img
                        src={require('../../../../../pic/33.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>任务监控</Text>
                </div>
                <div className='navItem'>
                    <Link 
                        onClick={handleToNotice}
                        to='/notice'
                        className='navBox'><img
                        src={require('../../../../../pic/35.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>任务发布</Text>
                </div>
                <div className='navItem'>
                    <Link 
                        to='/info'
                        className='navBox'><img
                        src={require('../../../../../pic/32.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>公式公告</Text>
                </div>
                <div className='navItem'>
                    <Link 
                        to='/file'
                        className='navBox'><img
                        src={require('../../../../../pic/21.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>文件管理</Text>
                </div>
                <div className='navItem'>
                    <Link 
                        to='people'
                        className='navBox'><img
                        src={require('../../../../../pic/3.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>个人中心</Text>
                </div>
            </div>

        </div>
    )
}

export default HotProcess
