import React from 'react'
import { message,Typography } from 'antd'
import { Link } from 'react-router-dom'

import '../Administration/Administration.scss'


const Administration = (e) => {


    const { Text } = Typography

    const handleToNotice = (e) =>{
        let rt = String(localStorage.getItem('right'))
        console.log('rt',typeof rt)
        if(rt !== '1' && rt !== '2'){
            e.preventDefault()
            message.info('当前用户无任务发布权限！')
        }
    }

    const handleToMonitor = (e) =>{
        let rt = String(localStorage.getItem('right'))
        console.log('rt',typeof rt)
        if(rt !== '1' && rt !== '3'){
            e.preventDefault()
            message.info('当前用户无任务监控权限！')
        }
    }

    return (
        <div className='Administration'>
            <div className='navWrap'>
            <div className='navItem'>
                    <Link
                        onClick={handleToMonitor}
                        to='/monitor' 
                        className='navBox'>
                        <img
                        src={require('../../../../../../pic/33.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>任务监控</Text>
                </div>
                <div className='navItem'>
                    <Link 
                        onClick={handleToNotice}
                        to='/notice'
                        className='navBox'><img
                        src={require('../../../../../../pic/35.png')}
                        alt='pic'
                    />
                    </Link>
                    <Text className='navItem_text'>任务发布</Text>
                </div> 
            </div>
        </div>
    )
}

export default Administration
