import React, { useEffect, useState, useContext } from 'react'
import { Spin, Typography, Modal, Button, Input, message } from 'antd'
import { Link } from 'react-router-dom'
import './InfoItem.scss'
// import RightContext from '../../../../../context/right/rightContext'
import InfoContext from '../../../../../context/info/infoContext'



const InfoItem = (props) => {

    //基本信息区域
    const { Text } = Typography
    const parantNode = document.querySelector('.Info')

    //实例化context
    const infoContext = useContext(InfoContext)
    const { getInfo, info = [], loading, title = '', content = '', } = infoContext


    useEffect(() => {  //useEffect 重构生命周期didMount
        getInfo(props.match.params.param)
        changeStyle1()
        return changeStyle2
        // eslint-disable-next-line
    }, [])


    const changeStyle1 = () => {

        if (parantNode) {
            parantNode.classList.add('InfoItem-add')
        }
    }

    const changeStyle2 = () => {

        if (parantNode) {
            parantNode.classList.remove('InfoItem-add')
        }
    }


    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='InfoItem'>
                <div className='navText'>
                    <Text strong style={{ color: '#ff0000' }} className='navFont'>{`公式公告 - ${title}`}</Text>
                    <Link to='/info' className='InfoItem-link' ><Button className='InfoItem-btn' type='danger'>返回</Button></Link>
                </div>
                <div className='InfoItem-detail'>
                    <Text className='InfoItem-detail__text'>
                        {
                            content
                        }
                    </Text>
                </div>
            </div>
        </Spin>
    }


    return (
        <div className='InfoItem'>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>{`公式公告 - ${title}`}</Text>
                <Link to='/info' className='InfoItem-link' ><Button className='InfoItem-btn' type='danger'>返回</Button></Link>
            </div>
            <div className='InfoItem-detail'>
                <Text className='InfoItem-detail__text'>
                    {
                        content
                    }
                </Text>
            </div>
        </div>
    )
}

export default React.memo(InfoItem)
