import React, { useEffect, useState, useContext } from 'react'
import { Spin, Typography, Table, Input, Tag, Button, Modal, message, Select } from 'antd'
import { Link } from 'react-router-dom'
import UserContext from '../../../../../../context/user/userContext'
import Spinner from '../../../../../laout/Spinner'
import { SyncOutlined } from '@ant-design/icons';

import qs from 'qs'
import axios from 'axios'
import '../SearchTable/SearchTable.scss'




const SearchTable = (props) => {

    //基本信息
    const imgUser = require('../../../../../../pic/27.png')
    const { Text } = Typography
    const { Search } = Input
    const { Option } = Select


    //实例化context
    const userContext = useContext(UserContext)
    // const { val,setMode, mode, getSearchTable, _table=[], _count=0, getDepartment, department = [], getDutyList, duty = [], getGroup, group = [], groupCount = 0, loading } = userContext
    const { setMode, val = '', mode = false, getUsers, users = [], usersNum = 0, getSearchUser, searchUser = [], searchUserNum = 0, loading } = userContext

    useEffect(() => {  //useEffect 重构生命周期didMount
        //由用户是否搜索来判断加载
        props.location.query ? getSearchUser(props.location.query.name, 5, 1) : getUsers(5, 1)
        return handleComponentWillUnMount

    }, [])

    const handleComponentWillUnMount = () => {
        setMode(false)
    }



    //分页
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `共${!mode ? usersNum : searchUserNum}条`,
        pageSize: 5,
        current: currentPage,
        total: !mode ? usersNum : searchUserNum,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)

        //按是否搜索进行 分页查询的切换页码
        !mode ? getUsers(5, v) : getSearchUser(val, 5, v)

    }


    //搜索区域
    const handleSearch = (v) => {
        if (!v) {
            message.info('请输入用户名来搜索~')
            return
        }
        setCurrentPage(1) //每次搜索重置页码
        getSearchUser(v, 5, 1)
    }

    //刷新按钮
    const handleRefresh = () => {
        getUsers(5, 1)
    }



    const columns = [
        {
            title: '头像',
            dataIndex: 'img',
            key: 'img',
            render: src => <span>
                {<img className='Table1-img'
                    src={src}
                    alt='pic'
                />}
            </span>,
        },
        {
            title: '姓名',
            dataIndex: 'name',
            key: 'name',
            className: 'antd_name',
        },
        {
            title: '联系方式',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '邮箱',
            dataIndex: 'mail',
            key: 'mail',
        },
        {
            title: '部门',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <span>
                    {tags.map(tag => {
                        let color = 'geekblue';

                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </span>
            ),
        },



    ];

    const data = [];
    if (!mode) {
        if (users.length > 0) {
            users.forEach((item, index) => {
                data.push({
                    key: item._id,
                    img: item.img ? item.img : imgUser,
                    name: item.name ? item.name : '未填写',
                    phone: item.phone ? item.phone : '未填写',
                    mail: item.mail ? item.mail : '未填写',
                    tags: item.right === 0 ? ['普通用户'] : (item.right === 1 ? ['管理员'] : (item.right === 2 ? ['发布者'] : (item.right === 3 ? ['监控者'] : '暂无'))),
                })
            })
        }
    } else {
        if (searchUser.length > 0) {
            searchUser.forEach((item, index) => {
                data.push({
                    key: item._id,
                    img: item.img ? item.img : imgUser,
                    name: item.name ? item.name : '未填写',
                    phone: item.phone ? item.phone : '未填写',
                    mail: item.mail ? item.mail : '未填写',
                    tags: item.right === 0 ? ['普通用户'] : (item.right === 1 ? ['管理员'] : (item.right === 2 ? ['发布者'] : (item.right === 3 ? ['监控者'] : '暂无'))),

                })
            })
        }
    }

    //处理表头兼容性问题
    let node = document.querySelector('.antd_name>span>div>.ant-table-column-title')
    if (node) {
        node.innerText = '姓名'
    }

    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='SearchTable'>
                <div className='navText' style={{ position: 'relative' }}>
                    <Text strong style={{ color: '#ff0000' }} className='navFont'>用户搜索</Text>
                    <div className='SearchTable-search' >
                        <Text style={{ minWidth: '65px' }}>搜索：</Text>
                        <Search
                            maxLength={20}
                            placeholder="输入用户名搜索"
                            onSearch={handleSearch}
                            style={{ width: 200 }}
                        />
                    </div>
                </div>

                <ul className='SearchTable-ul' style={{ position: 'relative', left: '10px' }}>
                    <li style={{ listStyle: 'none', position: 'relative', left: '20px', marginRight: '2%', display: 'flex', justifyContent: 'space-around', alignItems: 'center' }}>
                        <SyncOutlined
                            onClick={handleRefresh}
                            className='redLink ' style={{ fontSize: '23px', transform: 'translateY(-50%)', top: '50%', position: 'absolute' }} />
                    </li>
                    {mode &&
                        <li style={{ listStyle: 'none', position: 'relative', left: '30px' }}>
                            <span style={{ transform: 'translateY(-50%)' }} className='searchInfo'>为您搜索到<span style={{ marginLeft: '6px', marginRight: '6px', color: 'red' }}>{searchUserNum}</span>条数据～</span>
                        </li>
                    }
                    <Link to='/people/addressBook' className='SearchTable-link' ><Button className='SearchTable-btn' type='danger'>返回</Button></Link>
                </ul>
                <div className='SearchTable-wrap'>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>

            </div>
        </Spin>
    }




    return (
        <div className='SearchTable'>
            <div className='navText' style={{ position: 'relative' }}>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>用户搜索</Text>
                <div className='SearchTable-search' >
                    <Text style={{ minWidth: '65px' }}>搜索：</Text>
                    <Search
                        maxLength={20}
                        placeholder="输入用户名搜索"
                        onSearch={handleSearch}
                        style={{ width: 200 }}
                    />
                </div>
            </div>

            <ul className='SearchTable-ul' style={{ position: 'relative', left: '10px' }}>
                <li style={{ listStyle: 'none', position: 'relative', left: '20px', marginRight: '2%', display: 'flex', justifyContent: 'space-around', alignItems: 'center' }}>
                    <SyncOutlined
                        onClick={handleRefresh}
                        className='redLink ' style={{ fontSize: '23px', transform: 'translateY(-50%)', top: '50%', position: 'absolute' }} />
                </li>
                {mode &&
                    <li style={{ listStyle: 'none', position: 'relative', left: '30px' }}>
                        <span style={{ transform: 'translateY(-50%)' }} className='searchInfo'>为您搜索到<span style={{ marginLeft: '6px', marginRight: '6px', color: 'red' }}>{searchUserNum}</span>条数据～</span>
                    </li>
                }
                <Link to='/people/addressBook' className='SearchTable-link' ><Button className='SearchTable-btn' type='danger'>返回</Button></Link>

            </ul>
            <div className='SearchTable-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(SearchTable)
