import { CLEAR_RAW, GET_DONE_LIST, GET_MONITOR, SET_LOADING, GET_DETAIL, CLEAR_ID } from '../type'

export default (state, action) => {
    const { type, payload = {} } = action
    const {five,xArr1,xArr2,xArr3,xArr4,xArr5, uptime, t_in, t_out, total1, used, free, avaliable, shared, cached, load1, load5, load15, iowait, idle, system, user, raw_data2, raw_data3, raw_data4, raw_data5, doneList, doneListNum, loading, monitor, total, detail, currentId } = payload

    switch (type) {
        case SET_LOADING:
            return {
                ...state, //对state中状态字段解构
                loading: loading
            };

        case GET_MONITOR:
            return {
                ...state,
                monitor: monitor,
                total: total
            };

        case GET_DETAIL:
            return {
                ...state,
                detail: detail,
                currentId: currentId,
                //chart1
                iowait: iowait,
                idle: idle,
                system: system,
                user: user,
                //chart2数据
                load1: load1,
                load5: load5,
                load15: load15,

                //chart3数据
                total1: total1,
                used: used,
                free: free,
                avaliable: avaliable,
                shared: shared,
                cached: cached,

                //chart4数据
                t_in: t_in,
                t_out: t_out,

                //chart5数据
                uptime: uptime,
  
                //other
                xArr1: xArr1,
                xArr2: xArr2,
                xArr3: xArr3,
                xArr4: xArr4,
                xArr5: xArr5,
                five: five,

            }

        case CLEAR_ID:
            return {
                ...state,
                currentId: 0
            }

        case GET_DONE_LIST:
            return {
                ...state,
                doneList: doneList,
                doneListNum: doneListNum,
            }

        case CLEAR_RAW:
            return {
                ...state,
                //数据chart1
                iowait: [],
                idle: [],
                system: [],
                user: [],
                //chart2数据
                load1: [],
                load5: [],
                load15: [],
                //chart3数据
                total1: [],
                used: [],
                free: [],
                avaliable: [],
                shared: [],
                cached: [],
                //chart4数据
                t_in: [],
                t_out: [],
                //chart5数据
                uptime: [],
                //other
                xArr1: [],
                xArr2: [],
                xArr3: [],
                xArr4: [],
                xArr5: [],
                five: [],

            }


        default:
            return state;
    }

}