import React, { useEffect, useState, useContext } from 'react'
import { Spin, Typography, Table, Input, Tag, Button, Modal, message, Select } from 'antd'
import { Link } from 'react-router-dom'
// import GroupContext from '../../../../../context/group/groupContext'
import Spinner from '../../../../laout/Spinner'
import { SyncOutlined } from '@ant-design/icons';

import qs from 'qs'
import axios from 'axios'
import '../TeamManage/TeamManage.scss'

import UserContext from '../../../../../context/user/userContext'




const TeamManage = (e) => {

    //基本信息
    const imgUser = require('../../../../../pic/27.png')
    const { Text } = Typography
    const { Search } = Input
    const { Option } = Select
    const reg = /^([a-zA-Z\d])(\w|\-)+@[a-zA-Z\d]+\.[a-zA-Z]{2,4}$/ //邮箱正则验证


    //实例化context
    const userContext = useContext(UserContext)
    const { setMode, val = '', mode = false, getUsers, users = [], usersNum = 0, getSearchUser, searchUser = [], searchUserNum = 0, loading } = userContext




    useEffect(() => {  //useEffect 重构生命周期didMount
        getUsers(5, 1)
        return handleComponentWillUnMount
    }, [])

    const handleComponentWillUnMount = () => {
        setMode(false) //设置当前模式为普通模式
    }



    //新建成员数据区域
    const [username, setUserName] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [pwd, setPwd] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [trueName, setTrueName] = useState('')
    const [phone, setPhone] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [mail, setMail] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [sex, setSex] = useState('')

    //新建成员方法区域

    const handleUserNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setUserName(val.target.value)
    }

    const handleTrueNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setTrueName(val.target.value)
    }
    const handlePhoneChange = (val) => {
        if (val.target.value.length > 25) { return }
        setPhone(val.target.value)
    }
    const handlePwdChange = (val) => {
        if (val.target.value.length > 25) { return }
        setPwd(val.target.value)
    }
    const handleMailChange = (val) => {
        if (val.target.value.length > 35) { return }
        setMail(val.target.value)
    }
    const handleSexChange = (val) => {
        setSex(val)
    }

    const clearAdd = () => { //清除添加用户输入框
        setUserName('')
        setPwd('')
        setTrueName('')
        setPhone('')
        setMail('')
        setSex('')

    }

    //新建成员页面逻辑部分
    const showModal1 = () => {
        setVisible1(true)

    };

    const handleOk1 = e => {
        setVisible1(false)
        setPhone('')
        if (!username || !pwd || !trueName || !mail || !phone || !sex) {
            message.info('请正确输入用户信息！')
            clearAdd()
            return
        }
        if (username.length < 1 || username.length > 10) {
            message.info('用户名长度为10位之间！')
            clearAdd()
            return
        }
        if (trueName.length < 2 || trueName.length > 35) {
            message.info('真实姓名长度为2-35位之间！')
            clearAdd()
            return
        }
        if (pwd.length < 6 || pwd.length > 25) {
            message.info('请设置密码长度为6-25位之间！')
            clearAdd()
            return
        }
        if (phone.length < 8 || phone.length > 35) {
            message.info('联系方式长度为8-35位之间！')
            clearAdd()
            return
        }
        if (!reg.test(mail)) {
            message.info('填写的邮箱格式错误！')
            clearAdd()
            return
        }


        let _phone = Number(phone)
        let _sex = sex === '男' ? 1 : (sex === '女'?2:'')


        console.log(
            username, pwd, trueName, mail, _phone, _sex
        )

        console.log(
            typeof username, typeof pwd, typeof trueName, typeof mail, typeof _phone, typeof _sex
        )



        //添加成员 -- 默认添加成员权限为 right：0
        axios.post('/zjj/user/reg',
            qs.stringify({
                us: username,
                ps: pwd,
                name: trueName,
                sex: _sex,
                mail: mail,
                phone: _phone,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status !== 200) {
                    message.info('网络错误！')
                    clearAdd()
                    return
                }
                if (data.data.err === -2) {
                    message.info(data.data.msg)
                    clearAdd()
                    return
                }
                if (data.data.err === -3) {
                    message.info(data.data.msg)
                    clearAdd()
                    return
                }
                //请求成功后的操作
                clearAdd();
                setCurrentPage(1)
                message.info('新增成员成功');
                setTimeout(() => {
                    getUsers(5, 1)
                }, 200)
            }, () => {
                return Promise.reject('请求失败，请重试')
            }).catch(err => {
                message.info(err)
                console.log(err)
                clearAdd()
            })


    };

    const handleCancel1 = e => {
        setVisible1(false)
        clearAdd()

    };





    //弹窗
    const [visible, setVisible] = useState(false)
    const [visible1, setVisible1] = useState(false)
    const [visible2, setVisible2] = useState(false)

    //独立数据区域
    const [delName, setDelName] = useState('')//当前删除的用户



    //编辑成员数据区域
    const [_username, _setUserName] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [_trueName, _setTrueName] = useState('')
    const [_phone, _setPhone] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [_mail, _setMail] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管
    const [_sex, _setSex] = useState('')
    const [_editId, _setEditId] = useState('')
    //编辑成员方法区域

    const _handleUserNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        _setUserName(val.target.value)
    }

    const _handleTrueNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        _setTrueName(val.target.value)
    }
    const _handlePhoneChange = (val) => {
        if (val.target.value.length > 25) { return }
        _setPhone(val.target.value)
    }
    const _handleMailChange = (val) => {
        if (val.target.value.length > 35) { return }
        _setMail(val.target.value)
    }
    const _handleSexChange = (val) => {
        _setSex(val)
    }

    const clearEdit = () => { //清除添加用户输入框
        _setUserName('')
        _setTrueName('')
        _setPhone('')
        _setMail('')
        _setSex('')

    }



    //编辑成员页面逻辑部分
    const showModal2 = (id) => {
        setVisible2(true)
        if (!mode) {
            users.forEach(e => {
                if (e._id === id) {
                    _setUserName(e.us)
                    _setEditId(e._id)
                    _setTrueName(e.name)
                    _setPhone(e.phone)
                    _setMail(e.mail)
                    _setSex(e.sex)
                }
            })
        }
        else {
            searchUser.forEach(e => {
                if (e._id === id) {
                    _setUserName(e.us)
                    _setEditId(e._id)
                    _setTrueName(e.name)
                    _setPhone(e.phone)
                    _setMail(e.mail)
                    _setSex(e.sex)
                }
            })
        }


    };


    //编辑成员确认弹窗
    const handleOk2 = e => {
        setVisible2(false)
        // console.log(currentEdit)

        console.log(_username, _trueName, _mail, _phone, _sex)
        console.log(typeof _username, typeof _trueName, typeof _mail, typeof _phone, typeof _sex)

        if (!_username || !_trueName || !_mail || !_phone || !_sex) {
            message.info('请正确输入用户信息！')
            clearEdit()
            return
        }

        if (_trueName.length < 2 || _trueName.length > 35) {
            message.info('真实姓名长度为2-35位之间！')
            clearEdit()
            return
        }
        if (_phone.length < 8 || _phone.length > 35) {
            message.info('联系方式长度为8-35位之间！')
            clearEdit()
            return
        }
        if (!reg.test(_mail)) {
            message.info('填写的邮箱格式错误！')
            clearEdit()
            return
        }


        //编辑成员 -- 默认添加成员权限为 right：0
        axios.post('/zjj/user/updateUser',
            qs.stringify({
                _id: _editId,
                name: _trueName,
                sex: _sex,
                mail: _mail,
                phone: _phone,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status !== 200) {
                    message.info('网络错误！')
                    clearEdit()
                    return
                }
                if (data.data.err === -2) {
                    message.info(data.data.msg)
                    clearEdit()
                    return
                }
                if (data.data.err === -1) {
                    message.info(data.data.msg)
                    clearEdit()
                    return
                }
                //请求成功后的操作
                clearEdit();
                setCurrentPage(1)
                message.info('更新成员成功');
                setTimeout(() => {
                    getUsers(5, 1)
                }, 200)
            }, () => {
                return Promise.reject('请求失败，请重试')
            }).catch(err => {
                message.info(err)
                console.log(err)
                clearEdit()
            })
    };

    const handleCancel2 = e => {
        setVisible2(false)
        clearEdit()
    };

    //删除成员部分   
    const showModal = (id) => {
        setVisible(true)
        console.log('del: ' + id, typeof id)
        //mode===false  ： 普通模式
        //mode===true  ： 搜索模式

        if (!mode) {
            users.forEach(e => {
                console.log(e._id, typeof e._id)
                if (e._id === id) {
                    setDelName(e._id) //根据是否搜索来决定场景，来完成删除当前Del的成员
                }
            })
        } else {
            searchUser.forEach(e => {
                if (e._id === id) {
                    setDelName(e._id)
                }
            })
        }
    };

    const handleOk = e => {
        setVisible(false)
        console.log('ddd', delName)


        //删除成员 -- 默认添加成员权限为 right：0
        axios.post('/zjj/user/del',
            qs.stringify({
                _id: delName
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status !== 200) {
                    message.info('网络错误！')
                    clearAdd()
                    return
                }
                if (data.data.err === -2) {
                    message.info(data.data.msg)
                    clearAdd()
                    return
                }
                if (data.data.err === -1) {
                    message.info(data.data.msg)
                    clearAdd()
                    return
                }
                //请求成功后的操作
                //   clearAdd();
                setCurrentPage(1)
                message.info('删除成员成功');
                setTimeout(() => {
                    getUsers(5, 1)
                }, 200)
            }, () => {
                return Promise.reject('请求失败，请重试')
            }).catch(err => {
                message.info(err)
                console.log(err)
                //   clearAdd()
            })
    };

    const handleCancel = e => {
        setVisible(false)

    };

    //分页
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `共${!mode ? usersNum : searchUserNum}条`,
        // showTotal: () => `共${currentPage}条`,
        pageSize: 5,
        current: currentPage,
        total: !mode ? usersNum : searchUserNum,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)

        //按是否搜索进行 分页查询的切换页码
        !mode ? getUsers(5, v) : getSearchUser(val, 5, v)

    }


    //搜索区域
    const [currentSearch, setCurrentSearch] = useState('')
    const handleSearch = (v) => {
        if (!v) {
            message.info('请输入用户名来搜索~')
            return
        }
        setCurrentPage(1) //每次搜索重置页码
        getSearchUser(v, 5, 1)
    }

    //刷新按钮
    const handleRefresh = () => {
        getUsers(5, 1)

    }






    const columns = [
        {
            title: '用户名',
            dataIndex: 'username',
            key: 'username',
        },
        {
            title: '姓名',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '性别',
            dataIndex: 'sex',
            key: 'sex',
        },
        {
            title: '联系方式',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '邮箱',
            dataIndex: 'mail',
            key: 'mail',
        },
        {
            title: '权限',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <span>
                    {tags.map(tag => {
                        let color = 'geekblue';

                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </span>
            ),
        },
        {
            title: '操作',
            key: 'action',
            render: (text, record, index) => (
                <span>
                    <span
                        onClick={() => { showModal2(record.key) }}
                        className='redLink' style={{ color: 'red' }}>编辑</span>
                    <span
                        onClick={() => { showModal(record.key) }}
                        className='redLink' style={{ color: 'red', position: 'relative', left: '10px' }}>删除</span>
                </span>
            ),
        },



    ];

    const data = [];
    if (!mode) {
        if (users.length > 0) {
            users.forEach((item, index) => {
                data.push({
                    key: item._id,
                    username: item.us ? item.us : '未填写',
                    name: item.name ? item.name : '未填写',
                    sex: item.sex === 1 ? '男' : (item.sex === 2 ? '女' : '未填写'),
                    phone: item.phone ? item.phone : '未填写',
                    mail: item.mail ? item.mail : '未填写',
                    tags: item.right === 0 ? ['普通用户'] : (item.right === 1 ? ['管理员'] : (item.right === 2 ? ['发布者'] : (item.right === 3 ? ['监控者'] : '暂无'))),
                })
            })
        }
    } else {
        if (searchUser.length > 0) {
            searchUser.forEach((item, index) => {
                data.push({
                    key: item._id,
                    username: item.us ? item.us : '未填写',
                    name: item.name ? item.name : '未填写',
                    sex: item.sex === 1 ? '男' : (item.sex === 2 ? '女' : '未填写'),
                    phone: item.phone ? item.phone : '未填写',
                    mail: item.mail ? item.mail : '未填写',
                    tags: item.right === 0 ? ['普通用户'] : (item.right === 1 ? ['管理员'] : (item.right === 2 ? ['发布者'] : (item.right === 3 ? ['监控者'] : '暂无'))),

                })
            })
        }
    }

    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='TeamManage'>
                <div>
                    <Modal
                        title='提示'
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                    >
                        <p style={{ fontWeight: 'bold' }}>
                            确认删除此成员？
                    </p>
                    </Modal>
                </div>
                <div>
                    <Modal
                        title="编辑成员"
                        visible={visible2}
                        onOk={handleOk2}
                        onCancel={handleCancel2}
                        destroyOnClose={true}
                    >
                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">用户名</Text>
                            <Input
                                disabled
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入用户名"
                                value={_username}
                                onChange={_handleUserNameChange}
                            />
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text className="List-wrap__text">真实姓名</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入姓名"
                                value={_trueName}
                                onChange={_handleTrueNameChange}
                            />
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">性别</Text>
                            <Select
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请选择用户性别"
                                onChange={_handleSexChange}
                                value={_sex}
                            >
                                <Option disabled value={''} >请选择用户性别</Option>
                                <Option value={1} >男</Option>
                                <Option value={2} >女</Option>

                            </Select>
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text className="List-wrap__text">电子邮箱</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入邮箱"
                                value={_mail}
                                onChange={_handleMailChange}
                            />
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">联系方式</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入联系方式"
                                type='number'
                                value={_phone}
                                onChange={_handlePhoneChange}
                            />
                        </div>
                    </Modal>
                </div>

                <div>
                    <Modal
                        title="添加成员"
                        visible={visible1}
                        onOk={handleOk1}
                        onCancel={handleCancel1}
                        destroyOnClose={true}
                    >
                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">用户名</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入用户名"
                                value={username}
                                onChange={handleUserNameChange}
                            />
                        </div>
                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">密码</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入密码"
                                type='password'
                                value={pwd}
                                onChange={handlePwdChange}
                            />
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text className="List-wrap__text">真实姓名</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入姓名"
                                value={trueName}
                                onChange={handleTrueNameChange}
                            />
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">性别</Text>
                            <Select
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请选择用户性别"
                                onChange={handleSexChange}
                                value={sex}
                            >
                                <Option disabled value={''} >请选择用户性别</Option>
                                <Option value={'男'} >男</Option>
                                <Option value={'女'} >女</Option>

                            </Select>
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text className="List-wrap__text">电子邮箱</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入邮箱"
                                value={mail}
                                onChange={handleMailChange}
                            />
                        </div>

                        <div className='TeamManage-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="List-wrap__text">联系方式</Text>
                            <Input
                                style={{ marginBottom: '10px' }}
                                className="TeamManage-wrap__input"
                                placeholder="请输入联系方式"
                                type='number'
                                value={phone}
                                onChange={handlePhoneChange}
                            />
                        </div>
                    </Modal>
                </div>

                <div className='navText'>
                    <Text strong style={{ color: '#ff0000' }} className='navFont'>成员管理</Text>
                </div>
                <ul className='TeamManage-ul'>
                    <li
                        style={{ position: 'relative', bottom: '6px', marginRight: '2%' }}
                        className='TeamManage-ul__li' >
                        <Button
                            onClick={showModal1} className='TeamManage-ul__li--link' style={{ fontSize: '15px' }}>添加成员</Button>
                    </li>
                    <li style={{ listStyle: 'none', position: 'relative', marginRight: '2%' }}>
                        <SyncOutlined
                            onClick={handleRefresh} className='redLink refresh' style={{ fontSize: '23px' }} />
                    </li>
                    {mode &&
                        <li style={{ listStyle: 'none', position: 'relative' }}>
                            <span
                                className='searchInfo'>为您搜索到<span style={{ marginLeft: '6px', marginRight: '6px', color: 'red' }}>{searchUserNum}</span>条数据～</span>
                        </li>
                    }
                    <li className='TeamManage-ul__li' style={{ position: 'absolute', width: '30%', right: '5%' }} >
                        <div className='TeamManage-ul__search' >
                            <Text style={{ width: '65px' }}>搜索：</Text>
                            <Search
                                maxLength={15}
                                placeholder="输入用户姓名搜索"
                                onSearch={handleSearch}
                                style={{ width: 200 }}
                            />
                        </div>
                    </li>

                </ul>
                <div className='TeamManage-wrap'>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>

            </div>
        </Spin>
    }




    return (
        <div className='TeamManage'>
            <div>
                <Modal
                    title='提示'
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        确认删除此成员？
                    </p>
                </Modal>
            </div>
            <div>
                <Modal
                    title="编辑成员"
                    visible={visible2}
                    onOk={handleOk2}
                    onCancel={handleCancel2}
                    destroyOnClose={true}
                >
                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">用户名</Text>
                        <Input
                            disabled
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入用户名"
                            value={_username}
                            onChange={_handleUserNameChange}
                        />
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text className="List-wrap__text">真实姓名</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入姓名"
                            value={_trueName}
                            onChange={_handleTrueNameChange}
                        />
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">性别</Text>
                        <Select
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请选择用户性别"
                            onChange={_handleSexChange}
                            value={_sex}
                        >
                            <Option disabled value={''} >请选择用户性别</Option>
                            <Option value={1} >男</Option>
                            <Option value={2} >女</Option>

                        </Select>
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text className="List-wrap__text">电子邮箱</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入邮箱"
                            value={_mail}
                            onChange={_handleMailChange}
                        />
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">联系方式</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入联系方式"
                            type='number'
                            value={_phone}
                            onChange={_handlePhoneChange}
                        />
                    </div>
                </Modal>
            </div>

            <div>
                <Modal
                    title="添加成员"
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                    destroyOnClose={true}
                >
                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">用户名</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入用户名"
                            value={username}
                            onChange={handleUserNameChange}
                        />
                    </div>
                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">密码</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入密码"
                            type='password'
                            value={pwd}
                            onChange={handlePwdChange}
                        />
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text className="List-wrap__text">真实姓名</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入姓名"
                            value={trueName}
                            onChange={handleTrueNameChange}
                        />
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">性别</Text>
                        <Select
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请选择用户性别"
                            onChange={handleSexChange}
                            value={sex}
                        >
                            <Option disabled value={''} >请选择用户性别</Option>
                            <Option value={'男'} >男</Option>
                            <Option value={'女'} >女</Option>

                        </Select>
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text className="List-wrap__text">电子邮箱</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入邮箱"
                            value={mail}
                            onChange={handleMailChange}
                        />
                    </div>

                    <div className='TeamManage-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="List-wrap__text">联系方式</Text>
                        <Input
                            style={{ marginBottom: '10px' }}
                            className="TeamManage-wrap__input"
                            placeholder="请输入联系方式"
                            type='number'
                            value={phone}
                            onChange={handlePhoneChange}
                        />
                    </div>
                </Modal>
            </div>

            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>成员管理</Text>
            </div>
            <ul className='TeamManage-ul'>
                <li
                    style={{ position: 'relative', bottom: '6px', marginRight: '2%' }}
                    className='TeamManage-ul__li' >
                    <Button
                        onClick={showModal1} className='TeamManage-ul__li--link' style={{ fontSize: '15px' }}>添加成员</Button>
                </li>
                <li style={{ listStyle: 'none', position: 'relative', marginRight: '2%' }}>
                    <SyncOutlined
                        onClick={handleRefresh} className='redLink refresh' style={{ fontSize: '23px' }} />
                </li>
                {mode &&
                    <li style={{ listStyle: 'none', position: 'relative' }}>
                        <span
                            className='searchInfo'>为您搜索到<span style={{ marginLeft: '6px', marginRight: '6px', color: 'red' }}>{searchUserNum}</span>条数据～</span>
                    </li>
                }
                <li className='TeamManage-ul__li' style={{ position: 'absolute', width: '30%', right: '5%' }} >
                    <div className='TeamManage-ul__search' >
                        <Text style={{ width: '65px' }}>搜索：</Text>
                        <Search
                            maxLength={15}
                            placeholder="输入用户姓名搜索"
                            onSearch={handleSearch}
                            style={{ width: 200 }}
                        />
                    </div>
                </li>

            </ul>
            <div className='TeamManage-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(TeamManage)
