import React, { useReducer } from 'react';
import UserContext from './userContext';
import UserReducer from './userReducer';
import axios from 'axios'
import {message} from 'antd'

import { GET_INITIAL_USERS,SET_MODE,GET_USERS,GET_SEARCH_USER,GET_LOGINUSER,GET_ALLUSER,ClEAR_USER, SET_LOADING } from '../type'



const UserState = props => {
    //初始化状态
    const initialState = {
        list: {},
        allUser:[],//用户按权限分页查询
        total:0,//用户权限查询数据总数
        loading: false,
        searchUser:[], //用户按搜索分页查询
        searchUserNum:0,//搜索数据总数

        mode:false ,//mode--false：普通模式；  mode--true：搜索模式

        val:'',  //记录当前搜索的用户名
        users: [], //全部用户数目  -- 分页
        usersNum:0,
        initialUser: [], //全部用户数目 -- 未分页
        
    };

    //使用useReducer，即state关联Reducer
    const [state, dispatch] = useReducer(UserReducer, initialState) //函数参数1：当前提交的reduver       函数参数2：当前提交的状态值 ；

    //实现方法区域                                                          
    //getLoginUser获取登陆用户信息
    const getLoginUser = async _id => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态   
        const res = await axios.post('/zjj/user/getUserById',{Id:_id})
        if(res.data.err===-1){
            message.info('参数错误')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('用户查询失败，请重试')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('请重新登陆！')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_LOGINUSER,
            payload: {
                list: res.data.list[0]
            }
        })
        setLoading(false)
    };

     //按权限分页获取用户
     const getAllUser = async (right,limit,page) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        const res = await axios.post('/zjj/user/getUserByRight',{right,limit,page})
        if(res.data.err===-1){
            message.info('参数错误')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('请重新登陆！')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_ALLUSER,
            payload: {
                allUser: res.data.info.list,
                total: res.data.info.count,
            }
        })
        setLoading(false)
        setMode(false)

    };

     //按搜索分页获取用户
     const getSearchUser = async (Kw,limit,page) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        setMode(true)//改变当前分页查询为：按搜索数据

        const res = await axios.post('/zjj/user/getUserByKw',{Kw,limit,page})
        if(res.data.err===-1){
            message.info('参数错误')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('请重新登陆！')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_SEARCH_USER,
            payload: {
                searchUser: res.data.info.list,
                searchUserNum: res.data.info.count,
            }
        })
        setLoading(false)
    };


     //分页获取全部用户
     const getUsers = async (limit,page) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        const res = await axios.post('/zjj/user/getAllUser',{limit,page})
      
        if(res.data.err===-2){
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('请重新登陆！')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_USERS,
            payload: {
                users: res.data.info.list,
                usersNum: res.data.info.count,
            }
        })
        setLoading(false)
        setMode(false)

    };


    //获取全部用户
    const getInitialUser = async () =>{
        let res = null
        try{
           res = await axios.get('/zjj/user/getInitialUser',{})
        }
        catch(err){
            message.info(err)
            return Promise.reject()
        }
        if(res.status!==200){
            message.info('请求错误')
            setLoading(false)
            return
        }
        if(res.data.err===-2){
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        if(res.data.err=== -999){
            message.info('请重新登陆！')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_INITIAL_USERS,
            payload: {
                initialUser: res.data.info.list,
            }
        })
    }
 
    //clear users
    const clearUser = () => {
        dispatch({
            type: ClEAR_USER
        })
    };

    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //使用dispatch提交到Reducer中
            payload: { loading }
        })
    }

     //set mode : false === 未搜索数据 ;   true === 按搜索数据
     const setMode = (mode) => {
        dispatch({
            type: SET_MODE, 
            payload: { mode }
        })
    }

    return (<UserContext.Provider value={{
        list: state.list,                 //登陆用户数据
        allUser:state.allUser,            //用户权限查询数据
        total:state.total,                //用户权限查询数据总数
        searchUser:state.searchUser,      //用户搜索数据
        searchUserNum:state.searchUserNum,//用户搜索数据总数
        users:state.users,                //全部用户
        usersNum:state.usersNum,          //全部用户数
        loading: state.loading,
        mode: state.mode,
        initialUser: state.initialUser, //未分页全部用户数

        setLoading,
        clearUser,
        getLoginUser,
        getAllUser,
        getSearchUser,
        getUsers,
        setMode,
        getInitialUser,
    }}>{props.children}</UserContext.Provider>)


}
export default UserState
