import React, { useEffect, useState, useContext, useRef } from 'react'
import { Spin,Typography, Button,Table, Input, Tag, Modal, message } from 'antd'
// import { Link } from 'react-router-dom'

import Spinner from '../../../../laout/Spinner'
// import RightContext from '../../../../../context/right/rightContext'
import picDefault from '../../../../../pic/11.png'
import iconDefault from '../../../../../pic/20.png'
import axios from 'axios'
import qs from 'qs'
import InfoContext from '../.././../../../context/info/infoContext'

import '../PicManage/PicManage.scss'




const PicManage = (e) => {


    //基本信息
    const { Text } = Typography
    const fileUpload = useRef(null) //用于管理员上传缩略图
    const [path,setPath] = useState('') //存储上传后的轮播图路径


    //实例化context
    const infoContext = useContext(InfoContext)
    const { getAllPic, pic = [], loading } = infoContext
    const [delId, setDelId] = useState('')

    //生命周期
    useEffect(() => {  //useEffect 重构生命周期didMount
        getAllPic()

        return handleUnMount
        // eslint-disable-next-line
    }, [])

    
    //清除当前路径
    const clearPath =()=>{
        setPath('')
    }

    //组件销毁
    const handleUnMount = () =>{
        clearPath()
    }

    //弹窗    
    const [visible, setVisible] = useState(false)
    const [visible1, setVisible1] = useState(false)

    //删除轮播
    const showModal1 = (id) => {
        setVisible1(true)
        setDelId(id)
    };

    const handleOk1 = e => {
        setVisible1(false)
        //删除轮播
        axios.post('/zjj/pic/del', { _id:delId }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('参数错误!');
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info('删除轮播图失败！');
                        return
                    }
                    else {
                        message.info('删除轮播图成功~');
                        setTimeout(() => {
                            getAllPic()
                        }, 200)
                    }
                }
            })
        // axios.delete(`/AutomaticOfficeSystem/announcement/api/v1/shufflingFigure?id=${delId}`,{}).then(data => {
        //     if (data.status === 200 || data.status === 204) {
        //         message.info('删除轮播图成功~')
        //         setTimeout(()=>{
        //             getPic()
        //         },200)
        //     }
        //     else {
        //         message.info('删除轮播图失败，请重试！');
        //         return
        //     }
        // }).catch(err => {
        //     message.info(err)
        // })
    };

    const handleCancel1 = e => {
        setVisible1(false)
    };

    //添加轮播
    const showModal = () => {
        setVisible(true)
    };



    //上传轮播图
    const handleUpload = () => {

        console.log(fileUpload.current.files[0])
        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
        if (fileUpload.current.files[0] === undefined) {
            message.info('请上传轮播图！')
            return
        }

        //将图片存放在formData对象里
        let formData = new FormData()
        formData.append('file', fileUpload.current.files[0])


        axios.post('/zjj/file/upload', formData, config).then(data => {
            if (data.status === 200) {
                if (data.data.err === -1) {
                    message.info('上传文件过大！')
                    clearPath()
                    return
                }
                if (data.data.err === -2) {
                    message.info('上传文件类型不正确！')
                    clearPath()
                    return
                }
                let link = 'http://l1nkkk.xyz:3001' //生产模式
                // let link = 'http://localhost:3001'     //开发模式 
                let imgUrl = data.data.img

                console.log(imgUrl)
                // return updateImg(imgUrl)
                setPath(imgUrl)
                message.info('图片保存成功～')

            }
        }).catch(err => {
            message.info(err)
            clearPath()
        })
    }



    //上传路径
    const handleOk = e => {
        setVisible(false)
        if(!path){
            message.info('请上传轮播图！')
            return
        }

        axios.post('/zjj/pic/addPic', { path }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('参数错误');
                        clearPath()
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info('服务器错误,请刷新');
                        clearPath()
                        return
                    }
                    else {
                        message.info('上传轮播图成功~');
                        clearPath()
                        setTimeout(() => {
                            getAllPic()
                        }, 200)
                    }
                }
            })

        

        

    };

    const handleCancel = e => {
        setVisible(false)
        clearPath()
    };



    if(loading){return  <Spin className="myLoading" tip="加载中..."  size='large'>
        <div className='PicManage'>
            <div>
                <Modal
                    style={{zIndex:999}}
                    title='请添加轮播图片'
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <input type="file" ref={fileUpload} className='PicManage-wrap__input' />
                    <Button className='PicManage-btn' onClick={handleUpload} type='danger' >上传</Button>
                </Modal>
            </div>
            <div>
                <Modal
                    title='提示'
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        确认删除此信息？
                    </p>
                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>轮播图管理</Text>
                <Button className='PicManage-btn  PicManage-addBtn' onClick={showModal} type='danger'>添加图片</Button>

            </div>

            <div className='PicManage-wrap' style={{ overflowY: 'scroll', paddingBottom: '80px' }}>
                {pic.length>0 && pic.map(e=>(

                <div key={e._id} className='PicManage-wrap__div'>
                    <div className='PicManage-wrap__divLeft'>
                        <img  className='redLink' style={{ width: '100px', height: '100px' }} src={iconDefault} alt="default" />
                        <Text style={{ color: 'red', position: 'relative', left: '5px' }}>图片格式：1920*1080</Text>
                    </div>
                    <div className='PicManage-wrap__divRight' style={{flex:'1'}}>
                        <img style={{ position:'relative',left:'40px',height: '120px',width:'350px' }} src={e.img?e.img:picDefault} alt="pic" />
                    </div>
                    <div className='PicManage-wrap__divRight' >
                        <Text onClick={()=>showModal1(e._id)} className='redLink' style={{ color: 'red', position: 'relative', left: '5px' }}>删除</Text>
                    </div>
                </div>
    ))}   
            </div>
        </div>
    </Spin>}
    return (
        <div className='PicManage'>
            <div>
                <Modal
                    style={{zIndex:999}}
                    title='请添加轮播图片'
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <input type="file" ref={fileUpload} className='PicManage-wrap__input' />
                    <Button className='PicManage-btn' onClick={handleUpload} type='danger' >上传</Button>
                </Modal>
            </div>
            <div>
                <Modal
                    title='提示'
                    visible={visible1}
                    onOk={handleOk1}
                    onCancel={handleCancel1}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        确认删除此信息？
                    </p>
                </Modal>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>轮播图管理</Text>
                <Button className='PicManage-btn  PicManage-addBtn' onClick={showModal} type='danger'>添加图片</Button>

            </div>

            <div className='PicManage-wrap' style={{ overflowY: 'scroll', paddingBottom: '80px' }}>
                {pic.length>0 && pic.map(e=>(

                <div key={e._id} className='PicManage-wrap__div'>
                    <div className='PicManage-wrap__divLeft'>
                        <img  className='redLink' style={{ width: '100px', height: '100px' }} src={iconDefault} alt="default" />
                        <Text style={{ color: 'red', position: 'relative', left: '5px' }}>图片格式：1920*1080</Text>
                    </div>
                    <div className='PicManage-wrap__divRight' style={{flex:'1'}}>
                        <img style={{ position:'relative',left:'40px',height: '120px',width:'350px' }} src={e.img?e.img:picDefault} alt="pic" />
                    </div>
                    <div className='PicManage-wrap__divRight' >
                        <Text onClick={()=>showModal1(e._id)} className='redLink' style={{ color: 'red', position: 'relative', left: '5px' }}>删除</Text>
                    </div>
                </div>
    ))}   


            </div>

        </div>
    )
}

export default React.memo(PicManage)
