import React, { useEffect, useState, useContext } from 'react'
import { Spin, Typography, Table, Input, Tag, Icon, Modal, Select, Button, message } from 'antd'
// import RightContext from '../../../../../context/right/rightContext'
import Spinner from '../../../../laout/Spinner'
import qs from 'qs'
import axios from 'axios'
import UserContext from '../../../../../context/user/userContext'

import '../Right1/Right1.scss'




const Right1 = (e) => {

    //基本信息
    const { Text } = Typography
    const { Search } = Input
    const { Option } = Select
    const addType = '管理权限' //要添加的人员的权限类型

    //实例化context

    const userContext = useContext(UserContext)
    const { getInitialUser, initialUser = [], getAllUser, allUser = [], loading, total = 0, allpage = 0 } = userContext

    //生命周期
    useEffect(() => {  //useEffect 重构生命周期didMount、willUnMount

        getAllUser(1, 5, 1)
        getInitialUser()


        // eslint-disable-next-line
    }, [])



    //弹窗表单 -- 添加
    const [username, setName] = useState(''); // reactHook 重构： 使用useState重构state，进行状态接管

    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }


    //弹窗    
    const [visible, setVisible] = useState(false)
    const [visible2, setVisible2] = useState(false)

    //暂存的用户信息
    const [delName, setDelName] = useState('')
    const [delUserId, setDelUserId] = useState('')


    //用户添加区域
    const showModal = () => {
        setVisible(true)
    };

    const clearName = () => {
        setName('')
    }

    const handleOk = e => {
        setVisible(false)
        let flag = false
        if (!username) {
            message.info('请输入需添加的用户名!')
            clearName()
            return
        }

        initialUser.forEach(e => {
            if (e.us === username) {
                flag = true
                console.log(e.us, username, '+++', e._id)
                //添加成员 -- 用户权限提升为1:管理员权限
                axios.post('/zjj/user/updateRight',
                    qs.stringify({
                        _id: e._id
                    }),
                    {
                        headers: {
                            'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                            'Accept': 'application/json;charset=UTF-8'
                        }
                    }
                ).then(
                    (data) => {
                        console.log(data)
                        if (data.status === 200 || data.status === 201) {
                            if (data.data.err === -2) {
                                message.info(data.data.msg);
                                return
                            }
                            message.info('新增成员成功');
                            clearName();
                            setCurrentPage(1)
                            setTimeout(() => {
                                getAllUser(1, 5, 1)
                            }, 200)

                        }
                        else {
                            message.info('更改失败，请重试');
                            clearName();
                            return
                        }
                    }, () => {
                        return Promise.reject('请求失败，请重试')
                    }).catch(err => {
                        message.info(err)
                        clearName()
                    })
            }
        })
        if (!flag) { message.info('此用户不存在！') }




    };

    const handleCancel = e => {
        setVisible(false)
        clearName()
    };

    //删除区域
    const showModal2 = (id) => {
        setVisible2(true)
        console.log('deleteId= ' + id)
        setDelUserId(id)

    };

    const handleOk2 = e => {
        setVisible2(false)
        console.log(delUserId)

        //删除成员 -- 用户权限降低为0:普通用户权限
        axios.post('/zjj/user/reduceRight',
            qs.stringify({
                _id: delUserId
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {
                    if (data.data.err === -2) {
                        message.info(data.data.msg);
                        return
                    }
                    message.info('删除成员成功');
                    setCurrentPage(1)
                    setTimeout(() => {
                        getAllUser(1, 5, 1)
                    }, 200)

                }
                else {
                    message.info('更改失败，请重试');
                    return
                }
            }, () => {
                return Promise.reject('请求失败，请重试')
            }).catch(err => {
                message.info(err)
            })
    };

    const handleCancel2 = e => {
        setVisible2(false)
    };




    //分页
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `共${total}条`,
        pageSize: 5,
        current: currentPage,
        total: total,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
        getAllUser(0, 5, v)
    }

    // const paginationProps = {
    //     showSizeChanger: true,
    //     showQuickJumper: false,
    //     showTotal: () => `共${totals}条`,
    //     pageSize: this.state.pageSize,
    //     current: page.pageNum,
    //     total: page.total,
    //     onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
    //     onChange: (current) => this.changePage(current),
    // }






    const columns = [

        {
            title: '用户名',
            dataIndex: 'username',
            key: 'username',
            render: text => <span>{text}</span>,
        },
        {
            title: '姓名',
            dataIndex: 'name',
            key: 'name',
            render: text => <span>{text}</span>,
        },
        {
            title: '联系方式',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '邮箱',
            dataIndex: 'mail',
            key: 'mail',
        },
        {
            title: '职务',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <span>
                    {tags.map(tag => {
                        let color = 'geekblue';

                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </span>
            ),
        },
        {
            title: '操作',
            key: 'action',
            render: (text, record, index) => (
                <span>
                    {/* <span style={{ color: 'red' }}>编辑</span> */}
                    <span onClick={() => showModal2(record.key)} className='redLink' style={{ color: 'red', position: 'relative', left: '10px' }}>删除</span>
                </span>
            ),
        },

    ];

    const data = []; // 初始化antd表格
    if (allUser.length > 0) {
        allUser.forEach((item, index) => {
            data.push({
                key: item._id,
                username: item.us ? item.us : '未填写',
                name: item.name ? item.name : '未填写',
                sex: item.sex === 1 ? '男' : (item.sex === 2 ? '女' : '未填写'),
                phone: item.phone ? item.phone : '未填写',
                mail: item.mail ? item.mail : '未填写',
                tags: item.right === 0 ? ['普通用户'] : (item.right === 1 ? ['管理员'] : (item.right === 2 ? ['发布者'] : (item.right === 3 ? ['监控者'] : '暂无'))),
            })

        })
    }









    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='Right1'>
                <div>
                    <Modal
                        title="添加管理人员"
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        destroyOnClose={true}
                    >
                        <div className='Right1-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="Right1-wrap__text">职务</Text>
                            <Input
                                className="Right1-wrap__input"
                                placeholder="请输入审批权限"
                                value={addType}
                                disabled={true}


                            />
                        </div>
                        <div className='Right1-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="Right1-wrap__text">用户名</Text>
                            <Input
                                className="Right1-wrap__input"
                                placeholder="请输入用户名"
                                value={username}
                                onChange={handleNameChange}
                            />
                        </div>



                    </Modal>
                </div>

                <div>
                    <Modal
                        title='提示'
                        visible={visible2}
                        onOk={handleOk2}
                        destroyOnClose={true}
                        onCancel={handleCancel2}
                    >
                        <p style={{ fontWeight: 'bold' }}>
                            确认删除此成员？
                    </p>
                    </Modal>
                </div>

                <ul className='Right1-ul'>
                    <li className='Right1-ul__li Right1-ul__li--posi'   >
                        <Button onClick={showModal} style={{ border: "none" }}>
                            <Icon className='Right1-ul__li--icon' type="plus-square" />
                            <Text className='addText' style={{ position: 'relative', left: '5px' }}>添加成员</Text>
                        </Button>
                    </li>

                </ul>
                <div className='Right1-wrap'>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>

            </div>
        </Spin>
    }
    return (
        <div className='Right1'>
            <div>
                <Modal
                    title="添加管理人员"
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <div className='Right1-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="Right1-wrap__text">职务</Text>
                        <Input
                            className="Right1-wrap__input"
                            placeholder="请输入审批权限"
                            value={addType}
                            disabled={true}


                        />
                    </div>
                    <div className='Right1-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="Right1-wrap__text">用户名</Text>
                        <Input
                            className="Right1-wrap__input"
                            placeholder="请输入用户名"
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>



                </Modal>
            </div>

            <div>
                <Modal
                    title='提示'
                    visible={visible2}
                    onOk={handleOk2}
                    destroyOnClose={true}
                    onCancel={handleCancel2}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        确认删除此成员？
                    </p>
                </Modal>
            </div>

            <ul className='Right1-ul'>
                <li className='Right1-ul__li Right1-ul__li--posi'   >
                    <Button onClick={showModal} style={{ border: "none" }}>
                        <Icon className='Right1-ul__li--icon' type="plus-square" />
                        <Text className='addText' style={{ position: 'relative', left: '5px' }}>添加成员</Text>
                    </Button>
                </li>

            </ul>
            <div className='Right1-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(Right1)



