import React, { useState, useEffect, useContext } from 'react'
import { Spin, Typography, Input, Table, Tag } from 'antd'
import UserContext from '../../../../../../context/user/userContext'
import Spinner from '../../../../../laout/Spinner'

import '../Table1/Table1.scss'





const Table1 = (e) => {
    const imgUser = require('../../../../../../pic/27.png')

    const userContext = useContext(UserContext)
    const { getAllUser, allUser = [], loading, total = 0 } = userContext


    useEffect(() => {  //useEffect 重构生命周期didMount
        getAllUser(1, 5, 1)
        // eslint-disable-next-line
    }, [])

    const { Text } = Typography
    const { Search } = Input

    //分页
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `共${total}条`,
        pageSize: 5,
        current: currentPage,
        total: total,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
        getAllUser(1, 5, v)
    }



    const columns = [
        {
            title: '头像',
            dataIndex: 'img',
            key: 'img',
            render: src => <span>
                {<img className='Table1-img'
                    src={src}
                    alt='pic'
                />}
            </span>,
        },
        {
            title: '姓名',
            dataIndex: 'name',
            key: 'name',
            className: 'antd_name',
        },
        {
            title: '联系方式',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: '邮箱',
            dataIndex: 'mail',
            key: 'mail',
        },
        {
            title: '职务',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <span>
                    {tags.map(tag => {
                        let color = 'geekblue';

                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </span>
            ),
        },

    ];

    const data = []; // 初始化antd表格
    if (allUser.length > 0) {
        allUser.forEach((item, index) => {
            data.push({
                key: item._id,
                img: item.img ? item.img : imgUser,
                name: item.name ? item.name : '未填写',
                phone: item.phone ? item.phone : '未填写',
                mail: item.mail ? item.mail : (item.us ? item.us : '未填写'),
                tags: item.right === 0 ? ['普通用户'] : (item.right === 1 ? ['管理员'] : (item.right === 2 ? ['发布者'] : (item.right === 3 ? ['监控者'] : '暂无'))),

            })

        })
    }

    //处理表头兼容性问题
    let node = document.querySelector('.antd_name>span>div>.ant-table-column-title')
    if (node) {
        node.innerText = '姓名'
    }


    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='Table1'>
                <div className='Table1-table' style={{ marginBottom: '10px' }}>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>
            </div>
        </Spin>
    }
    return (
        <div className='Table1'>
            <div className='Table1-table' style={{ marginBottom: '10px' }}>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>
        </div>
    )
}

export default React.memo(Table1)




