import React, { useReducer } from 'react';
import InfoContext from './infoContext';
import InfoReducer from './infoReducer';
import axios from 'axios'
import { message } from 'antd'

import { GET_INFO,CLEAR_MESS,GET_MESS, GET_PIC, SET_LOADING } from '../type'



const InfoState = props => {
    //初始化状态
    const initialState = {
        pic: [], //轮播图列表 
        loading: false,

        mess: [], //信息列表
        messCount: 0, //信息总数

        info: [] , //信息列表 -- 未分页查询 -- 用于前台展示

        title:'',
        content:'',//单个信息的标题、 内容



    };

    //使用useReducer，即state关联Reducer
    const [state, dispatch] = useReducer(InfoReducer, initialState) //函数参数1：当前提交的reduver       函数参数2：当前提交的状态值 ；

    //实现方法区域                                                          
    //获取全部轮播图
    const getAllPic = async () => {
        setLoading(true)//只要开始请求数据，更改为loading状态   

        let res = null
        try {
            res = await axios.get('/zjj/pic/getAllPic', {})
        }
        catch (err) {
            message.info(err)
            return Promise.reject()
        }
        if (res.status !== 200) {
            message.info('请求错误')
            setLoading(false)
            return
        }
        if (res.data.err === -2) {
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        dispatch({
            type: GET_PIC,
            payload: {
                pic: res.data.list,
            }
        })
        setLoading(false)

    }


    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //使用dispatch提交到Reducer中
            payload: { loading }
        })
    }

    //分页获取全部信息
    const getMess = async (limit, page) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        const res = await axios.post(`/zjj/info/getAllInfo`,  {limit,page})

        if (res.data.err === -2) {
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }
        console.log(res.data.info.list,'ddd')
        dispatch({
            type: GET_MESS,
            payload: {
                mess: res.data.info.list,
                messCount: res.data.info.count,
            }
        })
        setLoading(false)

    };



    //获取全部信息 -- 未分页
    const getInfo = async (index) => { //根据login名获取user信息
        setLoading(true)//只要开始请求数据，更改为loading状态
        const res = await axios.get(`/zjj/info/getInitialInfo`,  {})
        if (res.data.err === -2) {
            message.info('查询失败，请重试')
            setLoading(false)
            return
        }

        //根据传入参数进行判断
        if(index){
            dispatch({
                type: GET_INFO,
                payload: {
                    info: res.data.list,
                    title: res.data.list[index-1].title,
                    content: res.data.list[index-1].content
                }
            })
        }else{
            dispatch({
                type: GET_INFO,
                payload: {
                    info: res.data.list,
                    title : '',
                    content: ''
                }
            })
        }

       
       
        setLoading(false)

    };


    //clearMess
    const clearMess = () => {
        dispatch({
            type: CLEAR_MESS
        })
    };


    return (<InfoContext.Provider value={{
        pic: state.pic,   //轮播图区域
        loading: state.loading,

        mess: state.mess, //信息区域
        messCount: state.messCount,

        info: state.info, //前台信息
        title: state.title,
        content: state.content,

        clearMess,
        getInfo,
        getMess,
        setLoading,
        getAllPic,
    }}>{props.children}</InfoContext.Provider>)


}
export default InfoState
