import React, { useEffect, useState, useContext, useRef } from 'react'
import { Spin, Typography, Table, Input, Tag, Button, Modal, message, Select } from 'antd'
import { Link } from 'react-router-dom'
import FileContext from '../../../../../context/file/fileContext'
import Spinner from '../../../../laout/Spinner'
import { SyncOutlined } from '@ant-design/icons';

import moment from 'moment'
import qs from 'qs'
import axios from 'axios'
import '../FileSearch/FileSearch.scss'



const FileSearch = (props) => {

    //基本信息
    const { Text } = Typography
    const { Search } = Input
    const { Option } = Select


    //实例化context
    const fileContext = useContext(FileContext)
    const { val, setMode, mode, _getFile, _file = [], _count = 0, loading } = fileContext

    console.log(props.location.query, '*')

    useEffect(() => {  //useEffect 重构生命周期didMount
        //由用户是否搜索来判断加载
        // console.log(props.location.query)
        props.location.query !== undefined && _getFile(props.location.query.name, 5, 1)
        return handleComponentWillUnMount
    }, [])

    const handleComponentWillUnMount = () => {
        setMode(false)
    }

    //  console.log(props.location.query,'qqq')


    //弹窗    
    const [visible, setVisible] = useState(false)

    //独立数据区域
    const [delId, setDelId] = useState('')//当前删除的文件id

    //删除文件区域
    const showModal = (id) => {
        setVisible(true)
        setDelId(id)
    };

    const handleOk = e => {
        setVisible(false)
        axios.post('/zjj/myFile/del', { _id: delId }).then(
            (data) => {
                if (data.status === 200) {
                    if (data.data.err === -1) {
                        message.info('参数错误!');
                        return
                    }
                    else if (data.data.err === -2) {
                        message.info('删除文件失败！');
                        return
                    }
                    else {
                        message.info('删除文件成功~');
                        setTimeout(() => {
                            props.history.push({ pathname: '/fileback' })
                        }, 200)
                    }
                }
            })
    };

    const handleCancel = e => {
        setVisible(false)
    };



    //分页

    const currentPageRef = useRef(1)
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `共${_count}条`,
        pageSize: 5,
        current: currentPage,//ref 代替 
        total: _count,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
        //按是否搜索进行 分页查询的切换页码
        _getFile(val, 5, v)




        // console.log('curr1=',currentPage)
        // console.log('curr2=',currentPageRef.current)
        // currentPageRef.current = v //保留页面于useRef
        // console.log('curr3=',currentP/ageRef.current)


    }


    //搜索区域
    const [currentSearch, setCurrentSearch] = useState(val)

    const handleSearch = (v) => {
        if (!v) {
            message.info('请输入文件名来搜索~')
            return
        }
        setCurrentPage(1) //每次搜索重置页码
        _getFile(v, 5, 1)
        setTimeout(() => {
            setCurrentSearch(v)
        })

    }


    //表格区域
    const columns = [
        {
            title: '名称',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: '上传时间',
            dataIndex: 'date',
            key: 'date',
        },
        {
            title: '上传人',
            dataIndex: 'username',
            key: 'username',
        },


        {
            title: '操作',
            key: 'action',
            render: (text, record, index) => (
                <span>
                    <span><a download href={record.link} style={{ color: 'red' }}>下载</a></span>
                    <span onClick={() => showModal(record.key)} className='redLink' style={{ color: 'red', position: 'relative', left: '10px' }}>删除</span>
                </span>
            ),
        },
    ];

    const data = [];
    if (_file.length > 0) {
        _file.forEach((item, index) => {
            data.push({
                key: item._id,
                title: item.name ? item.name : '未填写',
                date: item.time ? moment(Number(item.time)).format('YYYY-MM-DD') : '未填写',
                username: item.author ? item.author : '未填写',
                link: item.path ? item.path : ''
            })
        })
    }


    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='FileSearch'>
                <div>
                    <Modal
                        title='提示'
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                    >
                        <p style={{ fontWeight: 'bold' }}>
                            确认删除此信息？
                   </p>
                    </Modal>
                </div>
                <div className='navText' style={{ position: 'relative' }}>
                    <Text strong style={{ color: '#ff0000' }} className='navFont'>文件搜索</Text>
                    {mode && <div className='FileSearch-search' >
                        <Text style={{ minWidth: '65px' }}>搜索：</Text>
                        <Search
                            maxLength={20}
                            placeholder="请输入文件名来搜索"
                            onSearch={handleSearch}
                            style={{ width: 200 }}
                        />
                    </div>}
                </div>

                <ul className='FileSearch-ul'>

                    {mode &&
                        <li style={{ listStyle: 'none', position: 'relative', left: '20px' }}>
                            <span style={{ transform: 'translateY(-50%)' }} className='searchInfo'>为您搜索到<span style={{ marginLeft:'6px',marginRight:'6px',color: 'red' }}>{_count}</span>条数据～</span>
                        </li>
                    }
                    <Link to='/fileback' className='FileSearch-link' ><Button className='FileSearch-btn' type='danger'>返回</Button></Link>

                </ul>
                <div className='FileSearch-wrap'>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>

            </div>
        </Spin>
    }




    return (
        <div className='FileSearch'>
            <div>
                <Modal
                    title='提示'
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                >
                    <p style={{ fontWeight: 'bold' }}>
                        确认删除此信息？
                   </p>
                </Modal>
            </div>
            <div className='navText' style={{ position: 'relative' }}>
                <Text strong style={{ color: '#ff0000' }} className='navFont'>文件搜索</Text>
                {mode && <div className='FileSearch-search' >
                    <Text style={{ minWidth: '65px' }}>搜索：</Text>
                    <Search
                        maxLength={20}
                        placeholder="请输入文件名来搜索"
                        onSearch={handleSearch}
                        style={{ width: 200 }}
                    />
                </div>}
            </div>

            <ul className='FileSearch-ul'>

                {mode &&
                    <li style={{ listStyle: 'none', position: 'relative', left: '20px' }}>
                        <span style={{ transform: 'translateY(-50%)' }} className='searchInfo'>为您搜索到<span style={{ marginLeft:'6px',marginRight:'6px',color: 'red' }}>{_count}</span>条数据～</span>
                    </li>
                }
                <Link to='/fileback' className='FileSearch-link' ><Button className='FileSearch-btn' type='danger'>返回</Button></Link>

            </ul>
            <div className='FileSearch-wrap'>
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>

        </div>
    )
}

export default React.memo(FileSearch)
