import React, { useEffect, useState, useContext } from 'react'
import { Spin, Carousel, Typography, Modal, Button, Input, message } from 'antd'
// import RightContext from '../../../../../context/right/rightContext'
import Spinner from '../../../../laout/Spinner'
import InfoContext from '../../../../../context/info/infoContext'

import './InfoIndex.scss'
import { Link, Route } from 'react-router-dom'  //用于链接跳转路由，替换a标签，不会造成页面刷新，并且可以缓存数据


const InfoIndex = (e) => {
    const { Text } = Typography

    //实例化context
    const infoContext = useContext(InfoContext)
    const { getInfo, info = [], getAllPic, pic = [], loading } = infoContext


    useEffect(() => {  //useEffect 重构生命周期didMount
        getInfo()
        getAllPic() // 获取轮播图与信息

        // eslint-disable-next-line
    }, [])


    if (loading) {
        return <Spin>
            <div className='InfoIndex'>
                <div className='InfoIndex-top' style={{ position: 'relative', top: '-40px' }}>
                    <Carousel style={{ height: '500px', marginBottom: '30px' }} autoplay>

                        {pic.length > 0 && pic.map(e => (
                            <div key={e._id}>
                                <img src={e.img} style={{ width: '100%', height: '530px' }} alt="" />
                            </div>
                        ))}

                    </Carousel>
                </div>

                <div className="container" >
                    <div style={{ position: 'relative', width: '100%', height: '50px' }}>
                        <Text strong style={{ position: 'absolute', top: '50%', transform: 'translateY(-50%)', left: '50px', color: '#ff0000', textAlign: 'center' }} className='navFont'>公示公告</Text>
                    </div>

                    <div className='InfoIndex-bottom'>
                        {info.length === 0 &&
                            <div className='navText' style={{ borderBottom: 'none' }}>
                                <Text strong style={{ color: '#666' }} >暂无信息</Text>
                            </div>
                        }
                        {info.length > 0 && info.map((e, index) => (
                            <Link
                                key={e._id}
                                to={`/info/info${index + 1}`}
                                className='InfoIndex-bottom__div'>
                                <div className='InfoIndex-bottom__divLeft'><img style={{ width: '100px', height: '100px' }} src={e.img} className='redLink' /></div>
                                <div className='InfoIndex-bottom__divRight'>
                                    <Text className='redLink' style={{ fontSize: '25px' }} strong>{e.title}</Text>
                                    <p style={{ marginTop: '15px', maxHeight: '60px', overflow: 'hidden' }}>{e.content}</p>
                                </div>
                            </Link>
                        ))}
                    </div>
                </div>
            </div>
        </Spin>
    }
    return (<div>
        <div className='InfoIndex'>
            <div className='InfoIndex-top' style={{ position: 'relative', top: '-40px' }}>
                <Carousel style={{ height: '500px', marginBottom: '30px' }} autoplay>

                    {pic.length > 0 && pic.map(e => (
                        <div key={e._id}>
                            <img src={e.img} style={{ width: '100%', height: '530px' }} alt="" />
                        </div>
                    ))}

                </Carousel>
            </div>

            <div className="container" >
                <div style={{ position: 'relative', width: '100%', height: '50px' }}>
                    <Text strong style={{ position: 'absolute', top: '50%', transform: 'translateY(-50%)', left: '50px', color: '#ff0000', textAlign: 'center' }} className='navFont'>公示公告</Text>
                </div>

                <div className='InfoIndex-bottom'>
                    {info.length === 0 &&
                        <div className='navText' style={{ borderBottom: 'none' }}>
                            <Text strong style={{ color: '#666' }} >暂无信息</Text>
                        </div>
                    }
                    {info.length > 0 && info.map((e, index) => (
                        <Link
                            key={e._id}
                            to={`/info/info${index + 1}`}
                            className='InfoIndex-bottom__div'>
                            <div className='InfoIndex-bottom__divLeft'><img style={{ width: '100px', height: '100px' }} src={e.img} className='redLink' /></div>
                            <div className='InfoIndex-bottom__divRight'>
                                <Text className='redLink' style={{ fontSize: '25px' }} strong>{e.title}</Text>
                                <p style={{ marginTop: '15px', maxHeight: '60px', overflow: 'hidden' }}>{e.content}</p>
                            </div>
                        </Link>
                    ))}
                </div>
            </div>
        </div>
    </div>
    )
}

export default React.memo(InfoIndex)




